$mt-breakpoints: (
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
  xxl: 1400px,
);

:host {
  --mt-titulo-h2-padding: 4px 0 0 0;
  --mt-titulo-h2-margin: 0;
  --mt-titulo-h2-display: inline-block;

  color: #455a5a;
  font-family: 'Roboto', sans-serif;
  font-weight: 900;
  font-size: 20px;
  line-height: 1;
  margin: var(--mt-titulo-h2-margin);
  padding: var(--mt-titulo-h2-padding);
  border-top: 6px solid #ccdfdf;
  display: var(--mt-titulo-h2-display);
  letter-spacing: 1px;
}
